/*基本样式类*/
body,html {
    height:100%;
    -webkit-tap-highlight-color:transparent
}
a {
     color: #18b4ed;
     text-decoration: none;
     -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
 }
a:active {
    color: #18b4ed;
}
::-webkit-input-placeholder {
    color: #CCCCCC; }
.baseline {
    vertical-align: baseline; }
.middle {
    vertical-align: middle; }
.top {
    vertical-align: top; }
.block {
    display: block; }
.show {
    display: block; }
.hide {
    display: none; }
.inline {
    display: inline; }
.iblock {
    display: inline-block;
    *display: inline;
    *zoom: 1; }
.tleft {
    text-align: left; }
.tright {
    text-align: right; }
.tcenter {
    text-align: center; }
.center {
    margin:0 auto; }
.border {
    border: 1px solid #e0e0e0 !important; }
.border-t {
    border-top: 1px solid #e0e0e0;
}
.border-b {
    border-bottom: 1px solid #e0e0e0;
}
.border-tb {
    border-top: #e0e0e0 1px solid;
    border-bottom: #e0e0e0 1px solid;
    background-image: none;
}
.border-l {
    border-left: 1px solid #e0e0e0;
}
.border-r {
    border-right: 1px solid #e0e0e0;
}
.border-radius {
    border: 1px solid #e0e0e0;
    border-radius: 4px;
}
.dotted {
    border: 1px dotted #e0e0e0 !important; }
.dotted-b {
    border-bottom: 1px dotted #e0e0e0 !important; }
.hand {
    cursor: pointer; }
.move {
    cursor: move; }
.u {
    text-decoration: underline; }
.del {
    text-decoration: line-through; }
.-u{
    text-decoration: none; }
.i {
    font-style: italic; }
.-i {
    font-style: normal; }
.bold {
    font-weight: bold; }
.-bold {
    font-weight: normal; }
.-wrap {
    white-space: nowrap;
    word-break: break-all;
    overflow: hidden; }
.wrap {
    white-space: pre-wrap;
    word-wrap: break-word;
    word-break: break-all; }
.left {
    float: left; }
.right {
    float: right; }
.clear {
    clear: both; }
.clearfix:after, .clearfix:before {
    display: table;
    content: " "; }
.clearfix:after {
    clear: both; }
.upper {
    text-transform: uppercase; }
.lower {
    text-transform: lowercase; }
.cap {
    text-transform: capitalize; }
.hidden {
    overflow: hidden; }
.visible {
    overflow: visible; }
.rel {
    position: relative; }
.abs {
    position: absolute; }
.fixed {
    position: fixed; }
.indent {
    text-indent: 20px; }
.lheight {
    line-height: 22px; }
.outline {
    outline: none;}
.ell {
    text-overflow: ellipsis;
    overflow: hidden; }
.yahei {font-family: "Microsoft YaHei"; }
.hr1 {
    width:100%;
    margin:0 auto;
    border: 0;
    height:0;
    border-top: 1px solid rgba(0, 0, 0, 0.1);
}
.v-center{
    display:-webkit-box;
    display:-ms-flexbox;
    display:flex;-webkit-box-align: center;-ms-flex-align: center;align-items: center;-webkit-box-pack:center;-ms-flex-pack:center;justify-content:center;
}
.req:before{
    content: ' *';
    color: #EF4F4F;
    font-weight:bold;
}
/*页面整体样式*/
.container {
